/* eslint-disable react/display-name */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable react/prop-types */

import React, { forwardRef, useRef, useState } from 'react';
import { arrayOf, bool, func, node, number, oneOf, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import { Range, getTrackBackground } from 'react-range';
import shortid from 'shortid';
import { Manager, Reference, Popper } from 'react-popper';
import SliderLabel from '../slider-label';
import SliderLabelText from '../slider-label-text';

const CSS_PREFIX = 'vds-slider';
const CSS_STATE = 'vds-state';

const Slider = forwardRef(({
  className,
  customTrackColors,
  customLabel,
  disabled,
  id = shortid.generate(),
  label,
  markers,
  max,
  min,
  onChange,
  onFinalChange,
  tooltipPlacement,
  type,
  showTooltip,
  step,
  values,
  ...remainingProps
}, ref) => {
  const uniqueId = useRef(id);
  const uid = uniqueId.current;
  const [rangeValues, setRangeValues] = useState(values);
  const [enableTooltip, setEnableTooltip] = useState(false);
  ref = ref || useRef(null);

  return (
    <div
      className={classnames(CSS_PREFIX, {
        [`${CSS_STATE}--default-value`]: rangeValues.length === 1 && rangeValues[0] === min,
        [`${CSS_STATE}--disabled`]: disabled,
        [`${CSS_PREFIX}--${type}`]: Boolean(type)
      }, className)}
      ref={ref}
    >
      {
        label
          ? <SliderLabel htmlFor={`slider-${uid}`} id={`slider-label-${uid}`}>
            <SliderLabelText>{label}</SliderLabelText>
          </SliderLabel>
          : undefined
      }
      {
        typeof customLabel === 'function'
          ? customLabel(rangeValues, uid)
          : undefined
      }
      <Range
        disabled={disabled}
        max={max}
        min={min}
        onChange={v => {
          setRangeValues(v);
          if (typeof onChange === 'function') {
            onChange(v);
          }
        }}
        onFinalChange={onFinalChange}
        renderTrack={({ props, children }) => (
          <div
            {...props}
            className={`${CSS_PREFIX}-track`}
            style={{
              background: Boolean(customTrackColors) ? getTrackBackground({
                values: rangeValues,
                colors: customTrackColors,
                min,
                max
              }) : undefined,
              backgroundSize: type === 'discrete-number' &&  Boolean(customTrackColors) === false ? `calc(${step - 1}px) 4px` : undefined
            }}
          >
            {children}
            {
              ref && ref.current && Boolean(customTrackColors) === false
                ? <div
                  className={`${CSS_PREFIX}-range`}
                  style={{
                    backgroundSize: type === 'discrete-number' ? `calc(${step - 1}px) 4px` : undefined,
                    width: ref && ref.current ? ((rangeValues[0] - min) * ref.current.offsetWidth)/(max - min) : 0,
                    zIndex: -1
                }} />
                : undefined
            }
          </div>
        )}
        renderThumb={({ index, props, isDragged, value }) => (
          <div
            {...props}
            aria-labelledby={`slider-label-${uid}`}
            className={classnames(`${CSS_PREFIX}-thumb`)}
            id={`slider-${uid}`}
            onFocus={e => setEnableTooltip(true)}
            onKeyDown={e => {
              setEnableTooltip(true);
              props.onKeyDown(e);
            }}
            onMouseEnter={e => setEnableTooltip(true)}
            onMouseLeave={e => setEnableTooltip(false)}
            onBlur={e => setEnableTooltip(false)}
          >
          {
            showTooltip && enableTooltip
              ? <Manager>
              <Reference>
                {({ ref }) => (
                  <div ref={ref} />
                )}
              </Reference>
              <Popper placement={tooltipPlacement}>
                {({ ref, style, placement, arrowProps }) => (
                  <div
                    className={classnames('vds-tooltip', `${CSS_STATE}--show`)}
                    data-placement={placement}
                    ref={ref}
                    style={style}
                  >
                    <div className="vds-tooltip-content">
                      <span className="vds-tooltip-text">
                        {value}
                      </span>
                    </div>
                  </div>
                )}
              </Popper>
            </Manager>
              : undefined
          }
          </div>
        )}
        step={step}
        values={rangeValues}
      />
      {markers}
    </div>
  );
});

Slider.propTypes = {
  /**
   * @ignore
   */
  className: string,

  /**
   * Custom label markup
   */
  customLabel: func,

  /**
   * Custom track colors
   */
  customTrackColors: arrayOf(string),

  /**
   * Disabled
   */
  disabled: bool,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Label
   */
  label: node,

  /**
   * Markers
   */
  markers: node,

  /**
   * Max
   */
  max: number,

  /**
   * Min
   */
  min: number,

  /**
   * On Change
   */
  onChange: func,

  /**
   * On final change
   */
  onFinalChange: func,

  /**
   * Show tooltip
   */
  showTooltip: bool,

  /**
   * Step
   */
  step: number,

  /**
   * Tooltip placement
   */
  tooltipPlacement: oneOf([
    'auto',
    'bottom-end',
    'bottom-start',
    'bottom',
    'left-end',
    'left-start',
    'left',
    'right-end',
    'right-start',
    'right',
    'top-end',
    'top-start',
    'top'
  ]),

  /**
   * Type of slider
   */
  type: oneOf([
    'continuous-number',
    'discrete-number'
  ]),

  /**
   * Values
   */
  values: arrayOf(number),

  /**
   * Width
   */
  width: string
};

Slider.defaultProps = {
  max: 100,
  min: 0,
  step: 1,
  tooltipPlacement: 'top',
  values: [0]
};

Slider.displayName = 'Slider';

export default Slider;
