import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-slider-marker-value-icons';

const SliderMarkerValueIcons = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </div>
));

SliderMarkerValueIcons.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

SliderMarkerValueIcons.defaultProps = {
};

SliderMarkerValueIcons.displayName = 'SliderMarkerValueIcons';

export default SliderMarkerValueIcons;
