import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-slider-marker-values';

const SliderMarkerValues = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <ul
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </ul>
));

SliderMarkerValues.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

SliderMarkerValues.defaultProps = {
};

SliderMarkerValues.displayName = 'SliderMarkerValues';

export default SliderMarkerValues;
