import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-slider-label-text';

const SliderLabelText = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </span>
));

SliderLabelText.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

SliderLabelText.defaultProps = {
};

SliderLabelText.displayName = 'SliderLabelText';

export default SliderLabelText;
