import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-slider-label-value';

const SliderLabelValue = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </span>
));

SliderLabelValue.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

SliderLabelValue.defaultProps = {
};

SliderLabelValue.displayName = 'SliderLabelValue';

export default SliderLabelValue;
