import React, { forwardRef } from 'react';
import { elementType, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-slider-marker-value-item';

const SliderMarkerValueItem = forwardRef(({ children, className, tag: Tag, ...remainingProps }, ref) => (
  <Tag
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </Tag>
));

SliderMarkerValueItem.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Tag
  */
  tag: elementType
};

SliderMarkerValueItem.defaultProps = {
  tag: 'li'
};

SliderMarkerValueItem.displayName = 'SliderMarkerValueItem';

export default SliderMarkerValueItem;
